import Vue from 'vue'
import Router from 'vue-router'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'

// 
import index from 'components/index'
import kszl from 'components/kszl/kszl'
import login from 'base/login/login'

//
import gjgwy from 'components/kszl/children/gjgwy'
import gjzj from 'components/kszl/children/gjzj'
import gqzp from 'components/kszl/children/gqzp'
import hljgwy from 'components/kszl/children/hljgwy'
import hljzj from 'components/kszl/children/hljzj'
import jszg from 'components/kszl/children/jszg'
import jszp from 'components/kszl/children/jszp'
import nxs from 'components/kszl/children/nxs'
import tgjs from 'components/kszl/children/tgjs'
import yhzp from 'components/kszl/children/yhzp'
import zonghe from 'components/kszl/children/zonghe'

Vue.use(Router)
Vue.use(ElementUI)

export default new Router({
	routes: [
		{
			path: '/',
			redirect: '/index/'
		},
		{
			path: '/index/',
			component: index,
			meta:{
				title: '_',
			}
		},
		{
			name: '',
			icon: 'el-icon-printer',
			path: '/kszl/',
			component: kszl,
			children: [
				{
					path: '/kszl/gjgwy/',
					name: '',
					component: gjgwy,
					meta:{
						title: '_/',
					}
				},
				{
					path: '/kszl/gjzj/',
					name: '',
					component: gjzj,
					meta:{
						title: '_/',
					}
				},
				{
					path: '/kszl/gqzp/',
					name: '',
					component: gqzp,
					meta:{
						title: '_/',
					}
				},
				{
					path: '/kszl/hljgwy/',
					name: '',
					component: hljgwy,
					meta:{
						title: '_/',
					}
				},
				{
					path: '/kszl/hljzj/',
					name: '',
					component: hljzj,
					meta:{
						title: '_/',
					}
				},
				{
					path: '/kszl/jszg/',
					name: '',
					component: jszg,
					meta:{
						title: '_/',
					}
				},
				{
					path: '/kszl/jszp/',
					name: '',
					component: jszp,
					meta:{
						title: '_/',
					}
				},
				{
					path: '/kszl/nxs/',
					name: '',
					component: nxs,
					meta:{
						title: '_/',
					}
				},
				{
					path: '/kszl/tgjs/',
					name: '',
					component: tgjs,
					meta:{
						title: '_/',
					}
				},
				{
					path: '/kszl/yhzp/',
					name: '',
					component: yhzp,
					meta:{
						title: '_/',
					}
				},
				{
					path: '/kszl/zonghe/',
					name: '',
					component: zonghe,
					meta:{
						title: '_/',
					}
				},
			]
		},
		{
			path: '/login/',
			component: login,
			meta:{
				title: '_/',
			}
		},
	]
})



