<template>
  	<div class="zlbox">
		<div class="zlbox_list">
			<h3 class="contitle"><span>{{ this.$route.name }}</span></h3>
			<div class="zlsearch">
				<el-card class="zlcard" shadow="never">
					<div class="zlcardhead" slot="header">
			            <span class="subtitle"></span>
			        </div>
			        <div class="zlcardbody">
			        	<div style="margin-top: 10px;"></div>
			        	<el-input ref="searchinput" v-model.trim="search" placeholder="" prefix-icon="el-icon-edit" :clearable="true"></el-input>
			        	<el-row class="checkboxgroup">
			        		<span class="labeltitle"></span>
							<el-button type="text" v-for="hotword in hotwords" @click="hotsearch(hotword)" :key="hotword">{{ hotword }}</el-button>
						</el-row>
			        </div>
				</el-card>
			</div>
			<div class="zllist">
				<el-card class="zlcard" shadow="never" v-for="(zlitem,index) in zlsearch" :key="index">
					<div class="zlcardhead" slot="header">
						<el-tooltip class="item" effect="dark" :content="zlitem.filename" placement="bottom-start">
				            <span class="zlcardheadtitle">{{ zlitem.filename }}</span>
				        </el-tooltip>
			            <el-tag type="warning" size="small" v-if="zlitem.ksjd == ''"><i class="el-icon-edit"></i>{{ zlitem.ksjd }}</el-tag>
			            <el-tag type="danger" size="small" v-if="zlitem.ksjd == ''"><i class="el-icon-view"></i>{{ zlitem.ksjd }}</el-tag>
			            <el-tag type="success" size="small" v-if="zlitem.fileyear">{{ zlitem.fileyear }}</el-tag>
			        </div>
			        <el-scrollbar style="width: 100%; height: 120px;">
						<div class="zlcardbody">
							<svg class="icon" aria-hidden="true">
								<use :xlink:href='"#icon-" + zlitem.filetype'></use>
							</svg>
							<p v-html="zlitem.filedetail"></p>
						</div>
					</el-scrollbar>
					<div class="zlcardfoot">
						<el-button size="mini" @click.stop="zldownload(zlitem.fileaddress)"><i class="el-icon-download"></i></el-button>
						<span class="vardfootinfo" v-if="zlitem.downloadnum">{{ zlitem.downloadnum }}</span>
					</div>
				</el-card>
			</div>
		</div>
	</div>
</template>

<script>
export default {
	data () {
		return {
			zllists:[],  // 
			searchlist: [],
			hotwords:[this.$store.state.year,"",""],
			searched:false,
			search:''
		}
	},
	mounted: function(){
		this.getZldate()
	},
	computed: {
		zlsearch: function() {
        	var that = this;
        	const search = that.search;
            if (search) {
                return that.zllists.filter(items => {
                    return Object.keys(items).some(key => {
                        return String(items[key]).toLowerCase().indexOf(search) > -1
                    })
                })
                console.log(search)
            }
            return that.zllists
      	}
    },
	methods: {
		getZldate: function(argument) {  //
			var that = this
			that.$http.get('./static/json/kszl/hljgwy.json')
			.then(function(response){
				that.zllists = response.data
			})
			.catch(function(error){
				console.log("" + error);
			})
		},
		zldownload: function(fileaddress){
			var that = this;
			if (that.$store.state.userinfo.usertel == null || that.$store.state.userinfo.usertel == undefined) {
				this.$confirm(', ?', '', {
					confirmButtonText: '',
					cancelButtonText: '',
					type: 'warning'
				}).then(() => {
					this.$message({
						type: 'warning',
						message: ''
					});
					setTimeout(function(){
					 	that.$router.push({ path: '/login/' })
					}, 1000)
				}).catch(() => {
					//console.log('')
				})
			}else{
				window.open(fileaddress)
			}
		},
        hotsearch: function(val){
        	this.search = val
        	this.$refs.searchinput.focus()
        }
	}
}
</script>

<style lang="scss" scoped>

</style>



